.banner{
    position: relative;
    height: 450px;
    color: white;
    object-fit: contain;
}
.banner-contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner-title{
    font-size: 3rem;
    font-size: 800;
    padding-bottom: 0.3rem;
}
.banner-button{
    padding: 0.5rem 2rem;
    border: none;
    outline: none;
    margin-right: 1rem;
    cursor: pointer;
    color: white;
    font-weight: 800;
    background-color: rgba(51,51,51,0.5);
}
.banner-button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}
.banner-description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner-fadebottom{
    position: relative;
    transform: translateY(2px);
    height: 7.5rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
}